<template>
    <v-app>
        <v-container class="mt-6">
            <v-row no-gutters class="mx-auto">
                <v-col cols="12" xs="6" md="6" lg="6" >
                    <v-card class="mx-auto" >
                        <v-img :aspect-ratio="4/3" src="https://cdn.vuetifyjs.com/images/parallax/material.jpg">
                            <v-row align="end" class="lightbox white--text pa-2 fill-height">
                                <v-col>
                                    <div class="heading black--text">
                                        <span class="black--text">MODERN</span>
                                        <span class="primary--text">ERP</span>
                                    </div>
                                    <div class="body-1 black--text">Caique Ferreira</div>
                                </v-col>
                            </v-row>
                        </v-img>
                    </v-card>
                </v-col>
                <v-col cols="12" xs="6" md="6" lg="6" >
                    <v-form ref="form" @submit.prevent="login">
                        <v-card class="mx-auto">
                            <v-card-title>
                                <span class="black--text">MODERN</span>
                                <span class="primary--text">ERP</span>
                            </v-card-title>
                            <v-card-subtitle>
                                Version 0.0.1 
                                <h1 align="center" class="pb-1">Login</h1>
                                <v-alert v-if="message" type="error">
                                    {{message}} 
                                    <v-btn text small @click="message = null">
                                    <v-icon>mdi-close</v-icon>
                                    Close
                                    </v-btn>
                                </v-alert> 
                            </v-card-subtitle>
                            <v-card-text>
                                <v-row align="center" no-gutters >
                                    <v-col cols="12" sm="6" offset-sm="3" class="pt-2">
                                        <v-text-field
                                            v-model="user.username"
                                            label="Username or Email"
                                            placeholder="Username">
                                        </v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" offset-sm="3" class="pb-2">
                                        <v-text-field
                                            v-model="user.password"
                                            label="Password"
                                            placeholder="Password">
                                        </v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" offset-sm="3" class="pb-2">
                                        <v-checkbox label="Always on">
                                        </v-checkbox>
                                    </v-col>
                                </v-row>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn text color="blue darken-1">Reset your account</v-btn>
                                <v-btn text color="blue darken-1" :loading="loading" :disabled="loading" 
                                    @click="login">LogIn</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-form>
                </v-col>
            </v-row>
        </v-container>
    </v-app>
</template>

<script>
import User from '../models/user';

export default {
    name : "Login",
    components : {},
    data () {
        return {
            user: new User('user', 'password'),
            loading: false,
            message: ''
        }
    },
    methods: {
        login(){
            this.loading = true; 
            if(this.$refs.form.validate()) {
                this.$store.dispatch('auth/login', this.user).then(
                    () => {
                        this.$router.push('/company');
                    },
                    error => {
                        this.loading = false;
                        this.message =
                            (error.response && error.response.data) ||
                            error.message ||
                            error.toString();
                    }
                );
            }
        },
    },
}
</script>